.Hero{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;width:100%}.Hero__card{-webkit-flex-grow:1;flex-grow:1;max-width:80ch;padding:2rem}@media (max-width: 767px){.Hero__card{max-width:80%}}.Hero__card-text{font-family:var(--body-font);font-size:var(--fontSize-lg)}@media (max-width: 767px){.Hero__card-text{font-family:var(--body-font);font-size:var(--fontSize-normal)}}.AboutPage{width:100%}.AboutPage__card{-webkit-align-items:center;align-items:center;display:grid;grid:"title" "content" "redirect" / 1fr;margin:0 auto;padding:2rem;max-width:60%}@media (max-width: 767px){.AboutPage__card{max-width:100%}}.AboutPage__card-text{font-family:var(--body-font);font-size:var(--fontSize-normal)}@media (max-width: 767px){.AboutPage__card-text{font-family:var(--body-font);font-size:var(--fontSize-normal)}}.AboutPage__card-title{font-family:var(--body-font);font-size:var(--fontSize-xl);font-weight:var(--fontWeight-bold);margin:0}.AboutPage__card-link{align-self:center;color:var(--c-accent-purple);font-family:var(--body-font);font-size:var(--fontSize-lg);grid-area:redirect;text-decoration:none;text-transform:uppercase;text-align:center}@media (max-width: 767px){.AboutPage__card-link{font-size:1rem}}.AboutPage__card-link:hover,.AboutPage__card-link:focus{font-size:2rem;font-weight:700}.ContactPage{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:center;justify-content:center;width:100%}.ContactPage__card{-webkit-flex-grow:1;flex-grow:1;max-width:50%;padding:2rem}@media (max-width: 767px){.ContactPage__card{max-width:80%}}.ContactPage__card-text,.ContactPage__card-email{font-family:var(--body-font);font-size:var(--fontSize-lg)}.ContactPage__card-title{font-size:var(--fontSize-xl)}@media (max-width: 767px){.ContactPage__card-title{font-size:calc(var(--fontSize-xl) * 0.7)}}.ContactPage__card-email{font-size:2.5rem;font-weight:var(--fontweight-bold);padding:2rem;text-align:center;text-transfo