.Nav-svg{fill:var(--c-accent-purple);height:calc(var(--fontSize-xl) * 1.5);width:calc(var(--fontSize-xl) * 1.5)}.Nav-svg:hover{-webkit-transform:scale(1.5);transform:scale(1.5)}.About{-webkit-transform:rotate(-25deg);transform:rotate(-25deg)}.Resume{-webkit-transform:rotate(9deg);transform:rotate(9deg)}.Contact{-webkit-transform:rotate(-19deg);transform:rotate(-19deg)}.Meetup-container{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:center;justify-content:center}.Meetup-container>h1{margin:0 1.25rem}@media (max-width: 1024px){.Meetup-container>h1{font-size:calc(var(--fontSize-lg) * 0.9);margin:0 1rem}}

